<template>
  <div>
    <div class="tile is-ancestor">

      <div class="tile is-parent box">
        <article class="tile is-parent is-child is-5">
          <h1 class="title">Vault Login</h1>
          <div class="box is-parent is-6">
            <form id="form" v-on:submit.prevent="login">

              <div class="control">
                <label class="label">Authentication Type</label>
                <div class="select is-fullwidth">
                  <select v-model="type">
                    <option>Token</option>
                  </select>
                </div>
              </div>

              <p class="control has-icon">
                <input class="input" type="password" placeholder="Vault Token" v-model="vaultToken">
                <i class="fa fa-lock"></i>
              </p>

              <p class="control">
                <tooltip label="Use token 'sample-token' to simulate success" type="info" size="large" placement="bottom-right" :always="true" :rounded="true">
                  <button type="submit" value="Login" class="button is-success">
                    Login
                  </button>
                </tooltip>
              </p>

            </form>
          </div>
        </article>

        <article class="tile is-parent is-child is-7">
          <h1 class="title">Vault Health</h1>
          <div class="box">
            <div class="table-responsive">
              <table class="table is-striped is-narrow">
                <thead>
                  <tr>
                    <th>Key</th>
                    <th>Value</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="key in healthKeys">
                    <td>
                      {{ key }}
                    </td>
                    <td>
                      {{ healthData[key] }}
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </article>
      </div>

    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import Notification from 'vue-bulma-notification'
  import Tooltip from 'vue-bulma-tooltip'

  const NotificationComponent = Vue.extend(Notification)

  const openNotification = (propsData = {
    title: '',
    message: '',
    type: '',
    direction: '',
    duration: 4500,
    container: '.notifications'
  }) => {
    return new NotificationComponent({
      el: document.createElement('div'),
      propsData
    })
  }

  var fakeHealthData = {
    'initialized': true,
    'sealed': false,
    'standby': false,
    'server_time_utc': 1486763114,
    'version': '0.6.4',
    'cluster_name': 'vault-cluster-aa046aea',
    'cluster_id': 'dba7b2ff-7205-37d1-614f-744f40ee35a2'
  }

  export default {
    components: {
      Tooltip
    },

    data () {
      return {
        csrf: '',
        type: 'Token',
        vaultToken: '',
        healthData: {}
      }
    },

    mounted: function () {
      this.healthData = fakeHealthData
      openNotification({
        title: 'Demo mode',
        message: 'Goldfish is displaying fake data',
        type: 'warning'
      })
    },

    computed: {
      healthKeys: function () {
        return Object.keys(this.healthData)
      }
    },

    methods: {
      login: function () {
        switch (this.vaultToken) {
          case '':
            openNotification({
              title: 'Login failure',
              message: 'Empty authentication',
              type: 'danger'
            })
            break
          case 'sample-token':
            openNotification({
              title: 'Login success!',
              message: '',
              type: 'success'
            })
            break
          default:
            openNotification({
              title: 'Login failure',
              message: 'Invalid authentication',
              type: 'danger'
            })
            break
        }
      }
    }

  }
</script>

<style scoped>
  .button {
    margin: 5px 0 0;
  }

  .control .button {
    margin: inherit;
  }
</style>



